import React, { Component } from 'react'
import {
  CButton,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CRow
} from '@coreui/react'

class Lihatlaporan extends Component {
  constructor() {
    super()
    this.state = {
      modal : false,
    }
  } 
  pecahLineperLine = (string) => {
      return string.split('\n').map((item, index) => {
        return (index === 0) ? item : [<br key={index} />, item]
      })
    }

render() {
    return (
    <>
      <CButton 
        className="mr-1"
        color="info"
        size="sm"
        onClick={() => this.setState({modal : true})}
      >
        <a>Lihat</a>
      </CButton>
      <CModal 
        show={this.state.modal} 
        onClose={() => this.setState({modal : false})}
        size="lg"
        closeOnBackdrop={false}
      >
        <CModalHeader closeButton>
          <CModalTitle>
            <div>
            <h4>{this.props.judulrapat}</h4>  
            </div>  
          </CModalTitle>
        </CModalHeader>
        <CModalBody>
        {this.pecahLineperLine(String(this.props.laporanrapat))}
        </CModalBody>
        <CModalFooter>
          <div>
          <h4> oleh {this.props.picnotulis}</h4>
          </div>
        </CModalFooter>
      </CModal>
    </>
    );
  }
}
export default Lihatlaporan



