import React, { Component } from 'react';
import {
  CCard,
  CCardBody,
  CDataTable,
  CCardHeader
} from '@coreui/react'
import Tabletop from 'tabletop';
import Lihatlaporan from './lihatlaporan'

const fieldtabel =
[
    {key : "JudulRapat", label : "Judul Rapat", _style:{width: '30%'}},
    {key : "PesertaRapat", label : "Peserta Rapat"},
    {key : "TanggalRapat", label : "Tanggal Rapat"},
    {key :"PICNotulis", label : "PIC Notulis"},
    {key : "LaporanRapat", label : "Laporan Rapat"},
   
]
class Viavalen extends Component {
  constructor() {
    super()
    this.state = {
      data: [{JudulRapat: "Loading..", PesertaRapat: "Loading..", TanggalRapat: "Loading..", LaporanRapat: "Loading..", PICNotulis: "Loading.."}],
      loading: false
    }
  } 
    componentDidMount() { 
      Tabletop.init({
        key: '1nKFV8UA3Gtuq-nPeobb_FAmtT3O0Ebu43rkza0vW0po',
        callback: googleData => {
          this.setState({
            data: googleData
          });
          console.log("ini google data",googleData)        
        },
        simpleSheet: true
      })     
    }
render() {
    return (
      <div className="App">
        <CCard>
          <CCardHeader>
          <h1 className="App-title">Data Laporan Rapat Seksi Perancangan Sistem</h1>
          </CCardHeader>
            <CCardBody>
                <CDataTable
                    items={this.state.data}
                    fields={fieldtabel}
                    columnFilter
                    tableFilter
                    itemsPerPage={3}
                    pagination
                    scopedSlots = {{
                      'LaporanRapat':
                      (index)=>{
                            var judulrapat  = index.JudulRapat
                            var laporanrapat = index.LaporanRapat
                            var picnotulis  = index.PICNotulis
                            return (
                              <td>
                                <Lihatlaporan judulrapat={judulrapat} laporanrapat={laporanrapat} picnotulis={picnotulis} />
                              </td>
                            )
                          }
                    }}
                />
            </CCardBody>
        </CCard>
      </div>
    );
  }
}

export default Viavalen;